  <Current :gslocation="gsLocation" v-if="selectedTab == 'Current'" class="component-window"/>
  <Settings @newDayCount="newDayCount" @newLocation="newLocation" @newElev="newElev" v-if="selectedTab == 'Settings'" class="component-window"
  />
  <Menu @SettingsTab="selectedTab = 'Settings'" @CurrentTab="selectedTab = 'Current'" @ListTab="selectedTab = 'List'" class="main-menu"/>
